/* src/components/BlogCard.css */
.blog-card {
  border: 1px solid #e0e0e0;
  padding: 20px;
  margin: 10px;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.3s, box-shadow 0.3s;
}

.blog-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.blog-card h3 {
  font-size: 1.5em;
  margin-bottom: 10px;
  color: #333;
}

.blog-card p {
  margin: 5px 0;
  color: #666;
}

.blog-card .tags {
  margin-top: 10px;
}

.blog-card .tag {
  background-color: #007bff;
  color: white;
  padding: 5px 10px;
  border-radius: 3px;
  margin-right: 5px;
  font-size: 0.9em;
}

.blog-card .view-button {
  margin-top: 20px;
  text-align: center;
  color: #007bff;
  text-decoration: none;
  border: 1px solid #007bff;
  padding: 10px;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 0.3s, color 0.3s;
}

.blog-card .view-button:hover {
  background-color: #007bff;
  color: white;
}
