/* src/components/BlogDetail.css */
.blog-detail {
    padding: 20px;
    margin: 20px auto;
    max-width: 800px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  }
  
  .blog-detail h2 {
    font-size: 2em;
    margin-bottom: 10px;
    color: #333;
  }
  
  .blog-detail p {
    margin: 10px 0;
    color: #666;
    line-height: 1.6;
  }
  
  .blog-detail .tags {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  .blog-detail .tag {
    background-color: #007bff;
    color: white;
    padding: 5px 10px;
    border-radius: 3px;
    margin-right: 5px;
    font-size: 0.9em;
  }
  
  .blog-detail .description {
    font-style: italic;
    color: #444;
  }
  
  .blog-detail .content {
    margin-top: 20px;
    color: #555;
    line-height: 1.6;
  }
  