/* src/components/BlogList.css */
.search-sort-container {
  display: flex;
  margin: 20px 0;
  justify-content: center;
}

.blog-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.blog-list .blog-card {
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.filters {
  margin: 20px;
  width: 300px; /* Increased width for better usability */
  height: 40px; /* Increased height for better readability */
  padding: 0px 2px;
  border-radius: 8px; /* Increased border-radius for a smoother look */
  border: 1px solid #ccc; /* Added a border */
  background-color: #f9f9f9; /* Light background color */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  font-size: 16px; /* Increased font size for better readability */
  color: #333; /* Darker text color for better contrast */
  transition: box-shadow 0.3s ease; /* Smooth transition for focus effect */
}

.filters:focus {
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Enhanced shadow on focus */
  outline: none; /* Removed default outline */
  border-color: #007bff; /* Changed border color on focus */
}


